@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:600&display=swap&subset=cyrillic);
.header {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  background: #fff;
  height: 60px;
  box-shadow: 0 1px 3px 0px #38383854;
  z-index: 1000; }
  .header .header-cover {
    display: flex;
    justify-content: space-between;
    margin: auto; }
    .header .header-cover .header__logo {
      cursor: pointer;
      font-family: 'IBM Plex Sans', sans-serif;
      font-weight: 700; }
      .header .header-cover .header__logo > div {
        font-size: 1.8em;
        padding: 9px; }
        .header .header-cover .header__logo > div span:last-of-type {
          margin-left: 5px; }
    .header .header-cover .header__menu {
      display: flex;
      margin-left: auto; }
      .header .header-cover .header__menu .menu__item {
        height: 60px;
        font-size: 1.05em;
        padding: 0px 30px;
        display: flex;
        align-items: center; }
        .header .header-cover .header__menu .menu__item a {
          text-decoration: none;
          color: #000a12; }
          .header .header-cover .header__menu .menu__item a::before {
            content: "";
            border-bottom: 1px solid; }
    .header .header-cover .profile {
      margin-left: 10px; }
      .header .header-cover .profile .sign-up {
        padding: 21px; }
        .header .header-cover .profile .sign-up a {
          cursor: pointer;
          background-color: #000a12;
          color: #fff;
          border-radius: 4px;
          border: none;
          text-decoration: none;
          padding: 10px 25px;
          text-transform: uppercase;
          font-size: 0.9em; }

.home .wrap-image {
  max-width: 100%;
  height: 350px;
  background-image: url("/home/img-wrap.jpg");
  background-size: cover;
  background-position: center bottom;
  margin-top: 60px; }

.home .wrap-footer {
  position: relative;
  max-width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center bottom;
  margin-top: -175px;
  background: #0c1921; }

.home .header-cover {
  max-width: 960px; }

.home .group-status {
  position: absolute;
  top: -80px;
  right: 0;
  width: 200px;
  border-radius: 23px;
  color: #020a12;
  font-size: 1.1em;
  box-shadow: 0 1px 7px 2px #020a12;
  font-weight: bold; }
  .home .group-status .css-yk16xz-control {
    overflow: hidden;
    border-radius: 5px;
    border: 0px solid;
    padding: 7px; }

.home .categories {
  position: relative;
  margin-top: -130px;
  min-height: calc(100vh - 400px);
  background: #fff;
  z-index: 100;
  border-radius: 5px;
  box-shadow: 0 0px 7px 2px rgba(206, 206, 206, 0.47);
  padding-bottom: 40px;
  margin-bottom: 40px; }
  .home .categories .title {
    padding: 25px 40px 20px; }
  .home .categories .grid {
    display: grid;
    grid-template-columns: repeat(3, 273px);
    padding: 0px 40px;
    grid-gap: 30px; }
    .home .categories .grid__item {
      border-radius: 5px;
      box-shadow: 0 5px 16px 0 rgba(206, 206, 206, 0.47); }
      .home .categories .grid__item .image {
        width: 273px;
        height: 185px;
        background-position: center center;
        background-size: cover;
        border-radius: 5px; }
      .home .categories .grid__item .title {
        padding: 20px 15px; }
        .home .categories .grid__item .title h3 {
          font-size: 1.12em;
          margin: 0; }
      .home .categories .grid__item .status {
        text-align: right; }
        .home .categories .grid__item .status button {
          cursor: pointer;
          border: none;
          background: none;
          margin-right: 15px;
          margin-bottom: 15px;
          padding: 11px 13px;
          border-radius: 50%;
          box-shadow: 0 3px 9px 0 rgba(206, 206, 206, 0.45);
          -webkit-transition: .2s ease;
          transition: .2s ease;
          outline: none; }
          .home .categories .grid__item .status button:hover {
            box-shadow: 0px 3px 8px 1px rgba(0, 0, 0, 0.2); }
          .home .categories .grid__item .status button img {
            width: 22px; }
        .home .categories .grid__item .status button.active {
          color: #fff;
          background: #0c1921; }
  .home .categories .submit {
    text-align: right;
    padding: 0px 40px;
    margin-top: 40px; }
    .home .categories .submit button {
      outline: none;
      cursor: pointer;
      border: none;
      background-color: #000a12;
      color: #fff;
      text-transform: uppercase;
      font-size: 0.9em;
      border-radius: 26px;
      padding: 18px 45px;
      -webkit-transition: .3s ease;
      transition: .3s ease;
      box-shadow: 1px 4px 6px 0 rgba(206, 206, 206, 0.47); }
      .home .categories .submit button:hover {
        box-shadow: 1px 4px 16px 2px rgba(0, 0, 0, 0.47); }

.home {
  color: #2c2c2c;
  font-family: Arial, Tahoma, Bitstream Vera Sans, sans-serif;
  background: #f2f2f2; }
  .home .container {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto; }

.map .sidebar {
  position: fixed;
  left: 0px;
  top: 0px;
  bottom: 0px;
  width: 360px;
  background: #fff;
  z-index: 10;
  box-shadow: 0 5px 28px 0 rgba(206, 206, 206, 0.47); }
  .map .sidebar .header {
    position: relative;
    top: 60px;
    height: 50px;
    width: 100%;
    background: #020a12; }
  .map .sidebar .main {
    position: relative;
    top: 60px; }
    .map .sidebar .main .scrollarea {
      height: calc(100vh - 120px); }
    .map .sidebar .main .list__item {
      padding: 15px;
      border-bottom: 1px solid #ece9e9;
      margin-top: 10px; }
      .map .sidebar .main .list__item .image {
        width: 320px;
        height: 160px;
        background-position: center center;
        border-radius: 5px; }
      .map .sidebar .main .list__item img {
        width: calc(100% - 140px);
        height: 150px;
        margin: 0px 70px;
        border-radius: 50%; }
      .map .sidebar .main .list__item .name {
        font-size: 1.2em;
        padding: 15px 5px 5px; }
      .map .sidebar .main .list__item .category {
        padding: 0px 5px;
        color: #30363d; }


body {
  background: #f2f2f2;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

